/*----------------------------------
  Main Visual
------------------------------------*/
.mv {
  position: relative;
}

.mv__ph {
  img {
    width: 100%;
    max-width: none;
    height: 375px;
    object-fit: cover;
  }
}

.mv__inner {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  padding: 20px;
  color: $c_white;
}

.mv__catch {
  font-size: 2.4rem;
  font-weight: bold;
  text-align: center;
  text-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  span {
    font-size: 0.8rem;
  }
}

.mv__note {
  position: absolute;
  right: 10px;
  bottom: 10px;
  font-size: 1.2rem;
}

@include mq_min_md {
  .mv__ph {
    img {
      height: 500px;
    }
  }
  .mv__inner {
    left: 50%;
    max-width: $base_width;
    transform: translateX(-50%);
  }
  .mv__catch {
    font-size: 4rem;
  }
}
